body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color{
background: #DA4453;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #89216B, #DA4453); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.mdl-grid{
  
  padding-bottom: 20rem;
}

.mdl-layout__content{
  background: #ADA996;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #EAEAEA, #DBDBDB, #F2F2F2, #ADA996); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
 
}

.avatar{
  height: 250px;
  display: block;
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 20px;
}

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
  text-align: center;
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p{
  color: white;
  font-size: 20px;
  padding: 2rem;
  text-align: center;
}

.social-links{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 50%;
  margin: auto;
}

.social-links i{
  color: white;
  font-size: 5rem;
  padding: 8px;
}

.projects-grid{
  display: -webkit-flex;
  display: flex;
}

.contact-body{
  
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 2rem;
}

.contact-grid{
  
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80%;
  height: 550px;
  
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: 'Anton', sans-serif;
}

.contact-grid p{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-body hr{
  border-top: 5px solid black;
  width: 50%;
  margin: auto;
}

.contact-list i{
  font-size: 66px;
  padding-right: 3rem;
}

.resume-right-col{
  background: #27221F;
  color: white;
  padding: 5rem;
}
